import card from "@/components/common/card.vue"
import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import { mapMutations,mapState } from 'vuex'
export default {
	components: {
		card,
		uniListItem
	},
	data() {
		return {
			list:[
				{ title:"关于商城",path:"about" },
				{ title:"意见反馈",path:"" },
				{ title:"协议规则",path:"" },
				{ title:"资质证件",path:"" },
				{ title:"用户协议",path:"" },
				{ title:"隐私政策",path:"" },
			]
		}
	},
	computed: {
		...mapState({
			token:state=>state.user.token,
			loginStatus:state=>state.user.loginStatus
		})
	},
	methods: {
		...mapMutations(['logout','clearCart']),
		navigate(path){
			if (!path) return;
			uni.navigateTo({
				url: `/pages/${path}/${path}`
			});
		},
		// 退出登录
		logoutEvent(){
			this.$H.post('/logout',{},{
				token:false,
				toast:false
			}).then(res=>{
				// 退出登录
				this.logout();
				// 清空购物车
				this.clearCart()
				uni.showToast({
					title: '退出成功',
					icon: 'none'
				});
				uni.navigateBack({
					delta: 1
				});
			})
		}
	}
}
